input,
textarea,
button,
select {
  background: none;
  color: inherit;
  font-family: inherit;
  font-size: inherit;
  line-height: normal;
  border: 0;
  border-radius: 0;
  box-shadow: none;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
}

input[type="radio"] {
  -webkit-appearance: radio;
     -moz-appearance: radio;
          appearance: radio;
}

input[type="checkbox"] {
  -webkit-appearance: checkbox;
     -moz-appearance: checkbox;
          appearance: checkbox;
}

button {
  cursor: pointer;
}
